.photo {
    /* width: 100% */
    position: relative;
    border-bottom: 1px dashed #dbdbdb;
    box-sizing: border-box;
    text-transform: uppercase;
    text-align: center;
    padding-top: 100%;
    display: block;
}

.photo img {
    position: absolute;
    display: block;
    top: 0; 
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.dropArea {
    position: absolute;
    top: 0; 
    width: 100%;
    height: 100%;
}

.dragging {
    background: white;
    opacity: 0.8;
}

.message {
    position: absolute;
    top: 50%; 
    width: 100%;
}

.image {
    position: absolute;
    top: 0; left: 0;
    height: 100%;
    width: 100%;
    object-fit: cover;
}

.desc {
    padding: 16px;
    border-bottom: 1px dashed #dbdbdb;
}

.desc textarea {
    box-sizing: border-box;
    width: 100%;
    background: none;
    border: none;
    outline: none;
    font: inherit;
    padding: 0;
    
}

.actions {
    width:100%;
    box-sizing: border-box;
    padding:16px;
    display: flex;
    justify-content: space-between;
}

.actions button {
    color:#3897f0;
    font-weight: 600;
    font-size: 16px;
    /* margin-left:10px; */
}

.error {
    color:#FF1744;
    padding:4px 16px;
}
